.drop-menu{
    position: absolute;
    top: 80px;
    right: 20px;
    background-color: #FFFFFF;
    border-radius: 5px;
    padding: 10px 10px;
    width: 200px;

}
.drop-menu::before{
    content: '';
    position: absolute;
    top: -5px;
    right: 20px;
    height: 20px;
    width: 20px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.drop-menu.active{
    opacity: 1;
    visibility: visible;
    text-decoration: none;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    transition: 400ms ease;
}
.drop-menu.inactive{
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
    transition: 400ms ease;
}
